import React from 'react';
import ResaultDiagnosticContainer from './ResaultDiagnostic/ResaultDiagnosticContainer'
import s from "./FinishTests.module.css"
import { NavLink } from 'react-router-dom';

const FinishTests = (props) => {

      return (
            <div>
                  <div>
                        {props.resault[1] === undefined||props.resault[1].text === '' ?
                              <h5 className='text-center m-4'>       .</h5>
                              :
                              <div>
                                    <h3 className='text-center m-4'>   :</h3>
                                    <div className='m-4'>
                                          <ResaultDiagnosticContainer />
                                          <p>     ,  ,   .     . <span className={s.warning}>      (  )</span>.</p>
                                          <NavLink to="/Diagnostics"><button onClick={() => props.updateRes()} className="btn btn-warning">  </button></NavLink>
                                    </div>
                              </div>
                        }
                  </div>
            </div>
      );
}

export default FinishTests;
