import s from "./DiagnosticStarter.module.css"

const DiagnosticStarter4 = (props) => {
      return (
            <div className={`card m-1 ${s.card}`} >
                  <div className="card-body">
                        <h5 className="card-title"> </h5>
                        <h6 className="card-subtitle mb-2 text-muted2">   </h6>
                        <p className="card-text">   .</p>
                        <p className="card-text"></p>
                        <p className="card-text"></p>
                        <nav>
                              <NavLink to="/Diagnostics/Test/163"><button className={`btn btn-warning ${s.button}`}> </button></NavLink>
                        </nav>
                  </div>
            </div>
      );
}

export default DiagnosticStarter4;

